









import {Component}                  from 'vue-property-decorator';
import {mixins}                     from 'vue-class-component';
import TableContainer               from '../table/TableContainer.vue';
import MainMixin                    from '@/mixins/Main';
import LeagueSectionMixin           from '@/mixins/LeagueSection';
import TableContainerContainerMixin from '@/mixins/TableContainerContainer';

@Component({
    components: {
        TableContainer
    },
    name: `LeagueTable`
})
export default class LeagueTable extends mixins(MainMixin, LeagueSectionMixin, TableContainerContainerMixin)
{
    public rowsSortCompareFn(...args): number
    {
        // @ts-ignore
        return this.$store.state.leagueTableRecords.rowsSortCompareFn(...args);
    }

    public tableName = `leagueTableRecords`;
    public columns = {
        draws: {
            fut: false,
            order: 5,
            type: `integer`
        },
        form: {
            children: {
                class: {
                    d: `draw`,
                    l: `loss`,
                    otl: `overtime-loss`,
                    otw: `overtime-win`,
                    w: `win`
                },
                html: new this.$Object(this.text.form).map(([, column]) =>
                {
                    return column.short;
                }).value,
                title: new this.$Object(this.text.form).map(([, column]) =>
                {
                    return column.long;
                }).value,
                type: `alphabetical`
            },
            display: 450,
            order: 11
        },
        goalDifference: {
            display: 768,
            order: 9,
            signed: true,
            type: `integer`
        },
        losses: {
            order: 7,
            type: `integer`
        },
        matches: {
            order: 2,
            type: `integer`
        },
        overtimeLosses: {
            fut: true,
            order: 6,
            type: `integer`
        },
        overtimeWins: {
            fut: true,
            order: 4,
            type: `integer`
        },
        points: {
            order: 10,
            type: `integer`
        },
        rank: {
            order: 0,
            type: `index`
        },
        score: {
            join: `:`,
            order: 8,
            type: `integer[]`
        },
        userId: {
            dnfShow: true,
            order: 1,
            type: `id`
        },
        wins: {
            order: 3,
            type: `integer`
        }
    };
    thead = true;

    public get rows()
    {
        return this.rowsGet();
    }
}
